/*  */
import './index.less';
import React from 'react';
import { connect } from 'react-redux';
import { Layout, Button, Badge, Icon, Alert } from 'antd';
import Grid from 'views/grid/index.jsx';
import Menu from 'components/auxiliary/menu/index.jsx';
import Data from './data.js';

const { Header, Sider, Content } = Layout;
class LayoutContainer extends React.Component {
  toggle = () => {
    this.setState({
      collapsed: !this.state.collapsed
    });
  };
  menuCallback = (msg) => {
    this.props.setInfo('pageLoading', true);
    this.props.history.push({pathname: msg.params ? msg.path+msg.params : msg.path});
  };
  pathDeal = (path) => {
    if (path.indexOf(':') > 0) {
      return path.substring(0, Number(path.indexOf(':'))-1);
    }
    return path;
  };
  paramDeal = (param, path) => {
    return path.length>1 ? param.substring(path.length) : param;
  };
  componentDeal = (component, path) => {
    return path.length>1 ? component.substring(0, path.length) : component;
  };
  logout = () => {
    sessionStorage.clear();
    window.location.reload();
  };
  constructor(props) {
    super(props);
    this.state = {
      collapsed: false,
      defaultNode: null
    };
    this.menuCallback = this.menuCallback.bind(this);
    let arr = window.$globalmethod.flat(Data.menu, 'children');
    let node = arr.find(item => {
      let params = this.paramDeal(this.props.location.pathname, this.pathDeal(item.path));
      let path = this.componentDeal(this.props.location.pathname, this.pathDeal(item.path));
      return params ? path === this.pathDeal(item.path) && params === item.params : path === this.pathDeal(item.path);
    });
    if (node) {
      let nodelist = window.$globalmethod.leaflist(Data.menu, 'key', 'children', []);
      let defaultNode = window.$globalmethod.nodelist(nodelist, node, 'key', 'pkey', []);
      this.state.defaultNode = defaultNode;
    }
  };
  render() {
    return (
      <Layout className="layout">
        <Sider trigger={null}
          collapsible
          collapsed={ this.state.collapsed }>
          <div className={this.state.collapsed?'logo showLogo':'logo'}
            onClick={ () => window.location.reload() }>
            <i className="fa fa-leaf" aria-hidden="true"></i>
            <span>react+antd-CMS</span>
          </div>
          <Menu data={Data.menu}
            defaultNode={ this.state.defaultNode }
            collapsed={ this.state.collapsed }
            callback = { this.menuCallback } />
        </Sider>
        <Layout>
          <Header style={{ background: '#fff', padding: 0 }}>
            <Icon className="trigger"
              type={ this.state.collapsed ? 'menu-unfold' : 'menu-fold' }
              onClick={ this.toggle } />
            {window.$globalmethod.isMobile() && <Alert message={window.$globaldata.mobileMSG} type="warning" showIcon />}
            <Button type="link" onClick={this.logout}>
              <i className="fa fa-user-circle-o" aria-hidden="true"></i>
              {this.props.tiger.usrInfo.username}
            </Button>
          </Header>
          <Content style={{
              margin: '24px 16px',
              padding: 24,
              background: '#fff',
              minHeight: 280 }}>
            <Grid />
          </Content>
          <Badge className="notification" count={0} dot>
            <Icon type="notification" />
            Mock
          </Badge>
        </Layout>
      </Layout>
    );
  }
};

const mapStateToProps = (state) => {
  return {
    tiger: state
  }
};
const mapDispatchToProps = (dispatch) => {
  return {
    setInfo: (type, params) => dispatch({ type: type, params: params })
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(LayoutContainer);
