/*  */
import './index.less';
import React from 'react';
import { connect } from 'react-redux';
import { HashRouter as Router, Route, withRouter } from 'react-router-dom';
import { RoutersConfig } from 'router/index.jsx';
import { Row, Col, Empty, BackTop, Spin } from 'antd';

class Grid extends React.Component {
  routersPage = () => {
    let node = RoutersConfig.find(item => {
      return this.props.match.path === item.path
    });
    return window.$globalmethod.flat(node.children, 'children');
  };
  pathDeal = (path) => {
    if (path.indexOf(':') > 0) {
      return path.substring(0, Number(path.indexOf(':'))-1);
    }
    return path;
  };
  componentDeal = (component, path) => {
    return path.length>1 ? component.substring(0, path.length) : component;
  };
  hashListener = () => {
    this.props.setInfo('pageLoading', false);
  };
  componentDidMount () {
    window.addEventListener('hashchange', this.hashListener);
    this.props.setInfo('pageLoading', false);
  };
  componentWillUnmount() {       
    window.removeEventListener('hashchange', this.hashListener);
  };
  render() {
    let node = RoutersConfig.find(item => {
      return this.componentDeal(this.props.match.path, this.pathDeal(item.path)) === this.pathDeal(item.path);
    });
    let child = node.children;
    let component = child.find(item => {
      return this.componentDeal(this.props.location.pathname, this.pathDeal(item.path)) === this.pathDeal(item.path);
    });
    return (
      <Spin wrapperClassName="gridloading" tip={ window.$globaldata.loadingMSG } spinning={ this.props.tiger.pageLoading }>
        { component ?
          <Row className="grid">
            <Col className="gridContainer" span={24}>
              <BackTop target={ () => document.getElementsByClassName('grid')[0] }>
                <div className="ant-back-top-inner">UP</div>
              </BackTop>
              <div>{ this.props.tiger.pageLoading }</div>
              <Router>
                <Route path={ component.path }
                  component={ component.component }/>
              </Router>
            </Col>
          </Row> : <Empty description="" />
        }
      </Spin>
    );
  }
};

const mapStateToProps = (state) => {
  return {
    tiger: state
  }
};
const mapDispatchToProps = (dispatch) => {
  return {
    setInfo: (type, params) => dispatch({ type: type, params: params })
  }
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Grid));
