/*  */
import './index.less';
import React from 'react';
import { Menu } from 'antd';

const { SubMenu } = Menu;

class MenuPage extends React.Component {
  constructor(props) {
    super(props);
    let nodeList = this.props.defaultNode || window.$globalmethod.leafzero(this.props.data, 'children', []);
    let defaultSelectedKeys = [nodeList[nodeList.length-1].key];
    let defaultOpenKeys = nodeList.length>1 ? nodeList.slice(0, nodeList.length-1).flatMap(item => {
      return item.key
    }) : defaultSelectedKeys;
    this.state = {
      collapsed: this.props.collapsed,
      defaultSelectedKeys: defaultSelectedKeys || [],
      defaultOpenKeys: defaultOpenKeys || []
    };
  };
  render() {
    const subitem = (data) => {
      if (data.children) {
        return <SubMenu key={data.key}
          title={
            <span>
              <i className={data.icon} aria-hidden="true"></i>
              <span className={this.props.collapsed?'hideTitle':null}>{data.title}</span>
            </span>
          }>
          {data.children.map(item => {
            return subitem(item);
          })}
          </SubMenu>
      } else {
        return <Menu.Item key={data.key}>
          <i className={data.icon} aria-hidden="true"></i>
          <span className={this.props.collapsed?'hideTitle':null}>{data.title}</span>
        </Menu.Item>
      }
    };
    const callback = (msg) => {
      let arr = window.$globalmethod.flat(this.props.data, 'children');
      let node = arr.find(item => {
        return item.key === msg.key;
      });
      return this.props.callback(node);
    };
    return (
      <Menu className="menu"
        defaultSelectedKeys={this.state.defaultSelectedKeys}
        defaultOpenKeys={this.state.defaultOpenKeys}
        mode="inline"
        theme="dark"
        onSelect={callback}>
      {
        this.props.data.map(item => {
          return subitem(item);
        })
      }
      </Menu>
    );
  }
}

export default MenuPage;
